<template>
  <section class="section categories">
    <div class="container">
      <div class='categoriesHolder'>
        <h1>Please, select category you want to learn</h1>
        <ul>
          <li v-for="category in categories" v-on:click="selectCategory(category.id)">
            <div>{{ category.name }}</div>
          </li>

          <li v-bind:class="['add-new', {'inactive': !addNewMode}]" v-on:click="enterNewMode">
            <div v-if="!addNewMode" class="is-centered">
              <span class="is-size-4 fa fa-plus-square-o"></span>
            </div>
            <div v-show="isInProgress" class="spinner">
              <span class="fa fa-spinner"></span>
            </div>
            <div v-if="addNewMode" class="content">
              <div class="inputs">
                <div class="field">
                  <div class="control">
                    <input class="input" v-model="categoryInput" type="text" placeholder="Category Name"/>
                  </div>
                </div>
              </div>
              <div v-if="addNewMode" class="controls">
                <a v-on:click.stop="addNewCategory"><span class="is-size-5 fa fa-check check"></span></a>
                <a v-on:click.stop="exitNewMode"><span class="is-size-5 fa fa-remove cancel"></span></a>
              </div>
            </div>
          </li>

        </ul>
      </div>
    </div>
  </section>
</template>

<script>
  import ApiClient from '../services/ApiClient';
  export default {
    name: 'Categories',
    data () {
      return {
        categoryInput: ""
      }
    },

    computed: {
      isInProgress: function () {
        return this.$store.state.isInProgress;
      },
      addNewMode: function () {
        return this.$store.state.isInEdit;
      },
      categories: function () {
        return this.$store.state.categories;
      }
    },

    methods: {
      selectCategory: function (id) {
        this.$router.push({name: 'Category', params: {id: `${id}`}});
      },
      enterNewMode: function () {
        if (this.addNewMode) {
          return
        }
        this.categoryInput = "";
        this.$store.commit('setInEdit');
      },
      exitNewMode: function () {
        this.$store.commit('removeInEdit');
      },
      addNewCategory: function () {
        this.$store.dispatch('createCategory', {name: this.categoryInput, router: this.$router})
      }
    },

    created() {
      this.$store.dispatch('selectCategory', {id: null, flash_card_ids: []});

      ApiClient('/categories.json',
        {headers: this.$store.getters.authHeader},
        (error, json) => {
        if (error) {
          console.log('Error: ', error);
          this.categories = [];
        } else {
          this.$store.dispatch('setCategories', json);
        }
      });
    }

  }
</script>

<style lang="sass" scoped>
  @import "../values.sass"

  .categoriesHolder
    margin: 2 * $margin auto
    max-width: 1000px
    width: 100%
    box-sizing: border-box

  +mobile
    .categories
      padding-top: 0
    .categoriesHolder
      margin: 0 auto

  h1
    text-align: center
    padding: $margin
    @include medium_bold_font

  ul
    list-style: none
    width: 100%
    padding: 0
    display: flex
    align-items: flex-start
    justify-content: space-around
    flex-wrap: wrap

  li
    display: flex
    overflow-y: auto
    flex-basis: 300px
    box-sizing: border-box
    height: 120px
    width: 30%
    padding: 0 $margin
    margin-bottom: $margin
    border-radius: 10px
    background: $yellow
    @include defaultShadow
    @include small_bold_font
    color: black
    text-align: center
    cursor: pointer
    transition: all .1s ease-in-out
    div
      margin: auto

    &.add-new
      background-color: $yellow
      position: relative
      &:hover
        background-color: $yellow
      .content
        display: flex
        flex-direction: column
        margin: 0
        width: 100%
        .inputs
          display: flex
          margin: 0
          flex: 3
          .field
            width: 100%
            margin: auto 0
        .controls
          flex: 1
          a
            margin: 0 20px
            span
              text-shadow: 0px -1px 0px darken($yellow, 30%), 0px 1px 0px lighten($yellow, 10%)
            .check
              color: $main_color_dark
              &:hover
                color: $main_color_light
            .cancel
              color: $red_dark
              &:hover
                color: $red

      &.inactive
        background-color: transparent
        box-shadow: 0px 0px 0px
        border: 3px dashed $light_gray
        color: $light_gray
      .spinner
        position: absolute
        top: 35px
        left: 130px
        font-size: 40px
        z-index: 3
        color: $light_gray


  li:hover
    background: lighten($yellow, 10%)
    transform: scale(1.05)
    color: $gray
    border: 0
    @include defaultShadowRaise
</style>



