<template>
  <div id="app" class="application">
    <div class="navbar has-shadow">
      <div class="navbar-brand">
        <router-link to='/' class="navbar-item logo">
          <img src="./assets/logo.svg"/>
        </router-link>

          <router-link v-if="!loggedIn" to='/login' class="navbar-item">
            Sign In
          </router-link>

          <!--<router-link v-if="!loggedIn" to='/registration' class="navbar-item">-->
            <!--Sign Up-->
          <!--</router-link>-->

        <span v-if="loggedIn" class="navbar-item">
          Welcome, {{this.user}}!
        </span>

        <div class="navbar-burger" v-on:click="toggleNavbar" v-bind:class="{'is-active': isActive}">
          <span></span>
          <span></span>
          <span></span>
        </div>
      </div>

      <div class="navbar-menu" v-bind:class="{'is-active': isActive}">
        <!--<div class="navbar-end">-->
          <!--<router-link v-if="loggedIn" to='/categories' class="navbar-item">Categories</router-link>-->
        <!--</div>-->
        <div class="navbar-end">
          <a class='navbar-item' href="mailto:educardspublic@gmail.com">Contact Us</a>
          <a v-on:click="signout" v-if="loggedIn" to="/sign_out" class="navbar-item">
            <i class="fa fa-sign-out "></i>
            Sign Out
          </a>
        </div>
      </div>
    </div>

    <div class="app-wrapper">
      <router-view/>
    </div>

    <footer class="footer">
      <div class="container">
        <div class="content has-text-centered">
          <p class="is-size-7">
            &copy; 2017 Svitlana Dzyuban
          </p>
        </div>
      </div>
    </footer>
</div>
</template>

<script>
  export default {
    name: 'app',
    data: function () {
      return {
        isActive: false
      }
    },

    computed: {
      loggedIn: function () {
        return this.$store.state.auth.isLoggedIn
      },
      user: function () {
        return this.$store.state.user.name
      }
    },

    methods: {
      toggleNavbar: function () {
        this.isActive = !this.isActive
      },

      signout: function () {
        this.$store.dispatch('signout', {router: this.$router})
      }
    }
  }
</script>


<style lang="sass">
@import "../node_modules/bulma/bulma.sass"
@import "values.sass"
html
  background: linear-gradient(-60deg, #ffffff 0%,#f1f1f1 50%,#d1d1d1 50%,#f6f6f6 100%)
  background-attachment: fixed
  height: 100%
body
  background: url('assets/dot.svg')
  height: 100%

.navbar
  background: $main_color_light
  .navbar-brand .navbar-item:hover
    background: $main_color_light

  .navbar-menu
    &.is-active
      position: relative
      z-index: 9
    .navbar-item:hover
      background: $yellow

.application
  display: flex
  flex-direction: column
  .app-wrapper
    min-height: 80vh
  .footer
    padding: 0.5em
    @include small_default_font
    display: flex
    min-height: 3.25em
    background-color: transparent
    .container
      margin: auto
</style>



