<template>
  <div class="card">
    <div class="card-content">
      <h1 class="title">
        {{this.info.name}}
      </h1>
      <div class="content">
        <div class="columns is-mobile">
          <div class="column">
            <div class="is-flex">
              <div>Total cards</div>
              <cards-svg :text="totalCards" width="60" height="60"></cards-svg>
            </div>
            <div>
              Total points
              <span class="is-bold">0</span>
            </div>
          </div>
          <div class="column">
            <div class="is-flex">
              <div>Cards learnt</div>
              <cards-svg :text="0" width="60" height="60"></cards-svg>
            </div>
            <div>
              Last points
              <span class="is-bold">0</span>
            </div>
          </div>
        </div>
        <div class="columns">
          <div class="column buttonHolder">
            <!--<button-svg text="Let's Start" v-on:click.native="startSession"></button-svg>-->
            <div class="button is-primary" v-on:click.once="startSession">Let's Start</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import CardsSvg from "./svg/CardsSvg.vue";
  import ButtonSvg from "./svg/Button.vue";
  import ApiClient from '../services/ApiClient';

  export default {
    name: 'CategoryInfo',

    components: {
      'button-svg': ButtonSvg,
      'cards-svg': CardsSvg
    },

    props: {
      id: String
    },

    data() {
      return {
        info: {flash_card_ids: []}
      }
    },

    methods: {
      startSession: function () {
        this.$store.dispatch('startSession');
      }
    },

    computed: {
      totalCards: function () {
        return this.info.flash_card_ids.length
      }
    },

    created() {
      ApiClient(`/categories/${this.id}.json`,
        {headers: this.$store.getters.authHeader},
        (error, json) => {
        if (error) {
          console.log('Error: ', error);
          this.$store.dispatch('selectCategory', {id: null, flash_cards: []});
          this.$router.push({name: 'Categories'});
        } else {
          this.$store.dispatch('selectCategory', {id: this.id, flash_card_ids: json.flash_card_ids});
          this.info = json;
        }
      });
    }
  }
</script>
<style lang="sass" scoped>
  @import "../values.sass"
  .card
    @include small_default_font

  .title
    @include medium_bold_font

  .is-flex
    div
      margin: auto 0
    svg
      margin-left: 10px

  .buttonHolder
    display: flex
    .button
      cursor: pointer
      margin: auto
      margin-top: $margin * 2
      height: 3rem
      padding: 0 3em
</style>



