<template>
  <div class="flipping-area">
    <div class="flipper card is-flex" v-bind:class="{flipped: isFlipped}">
      <div class="card-content backface" v-if="isFlipped">
        <div>{{this.back}}</div>
      </div>

      <div class="card-content frontface" v-else>
        <div>{{this.face}}</div>
      </div>
    </div>
  </div>
</template>

<script>
  import ApiClient from '../services/ApiClient';

  export default {
    name: 'Card',

    props: ['id', 'back', 'face', 'isFlipped'],

    data: function () {
      return {}
    }
  }

</script>

<style lang="sass" scoped>
  @import "../values.sass"
  .flipping-area
    perspective: 1000px

    .flipper.flipped
      transform: rotateY(180deg)
      background-color: $yellow

    .flipper
      transition: 0.2s
      position: relative

      .backface, .frontface
        width: 100%
        height: 100%
        position: absolute
        top: 0
        left: 0
        display: flex
        div
          margin: auto
          text-align: center

      .frontface
        z-index: 2
        transform: rotateY(0deg)
      .backface
        z-index: 2
        transform: rotateY(180deg)

  .card
    .card-content
      margin: auto
      @include large_bold_font

    min-height: 50vh
    color: black

    .backface
      @include large_light_font


</style>



