<template>
  <section class="section category">
    <div class="container">
      <stars v-if="sessionStarted"></stars>

      <div class="columns">

        <div class="column is-half is-offset-one-quarter" v-if="sessionStarted">
          <card v-bind:id="card.id" v-bind:isFlipped="cardIsFlipped" v-bind:face="card.face"
                v-bind:back="card.back"></card>
          <div class="is-flex buttonHolder">
            <div class="button is-danger" v-on:click="flipCard">I Don't Know</div>
            <div class="button is-primary" v-if="lastCard" v-on:click.once="finishSession">Finish</div>
            <div class="button is-primary" v-else v-on:click="nextCard">Next</div>
          </div>
        </div>

        <div class="column categoryInfoHolder is-narrow" v-else>
          <category-info v-bind:id="id"></category-info>
        </div>

        <finish-dialog v-if="showFinishDialog"></finish-dialog>
      </div>
    </div>
  </section>
</template>

<script>
  import CategoryInfo from "./CategoryInfo.vue";
  import Card from "./Card.vue";
  import FinishDialog from "./FinishDialog.vue";
  import Stars from "./Stars.vue";

  export default {
    name: 'Category',

    components: {
      'category-info': CategoryInfo,
      'card': Card,
      'finish-dialog': FinishDialog,
      'stars': Stars
    },

    data: function () {
      return {
        cardIsFlipped: false
      }
    },

    props: {
      id: String
    },

    computed: {
      sessionStarted: function () {
        return this.$store.state.session.started
      },
      lastCard: function () {
        return this.$store.getters.isLastQuestion;
      },
      card: function () {
        let currentQuestion = this.$store.state.session.currentQuestion;

        return this.$store.state.session.questions[currentQuestion];
      },
      showFinishDialog: function () {
        return this.$store.state.session.isFinished;
      }
    },
    methods: {
      flipCard: function () {
        this.cardIsFlipped = !this.cardIsFlipped;
        this.$store.dispatch('dontKnow');
      },
      nextCard: function () {
        this.cardIsFlipped = false;
        this.$store.dispatch('nextCard');
      },
      finishSession: function () {
        this.$store.dispatch('finishSession');
      }
    }
  }
</script>

<style lang="sass" scoped>
  @import "../values.sass"

  .categoryInfoHolder
    margin: auto

  .buttonHolder .button:first-child
    margin-right: 1.5rem

  .buttonHolder
    margin-top: 1.5rem
    justify-content: space-around
    .button
      flex: 1
      height: 4rem

  +mobile
    .category
      padding-top: 1.5em
    .categoryInfoHolder
     margin-top: 0px

</style>



