function ApiClient(resourceURI, options, callback) {
  let headers = new Headers();
  headers.append('Content-Type', 'application/json');

  for (const header in options.headers) {
    headers.append(header, options.headers[header])
  }

  let baseURI = 'http://educards.herokuapp.com';

  let init = {
    method: options.method || 'GET',
    headers: headers,
    mode: 'cors',
    body: options.body
  };

  let request = new Request(baseURI + resourceURI, init);

  fetch(request).then(function(response) {
    let json = response.json();

    if(response.ok) {
      return json;
    } else {
      return json.then(error => {throw error;});
    }
  }).then((json) => {
    callback(null, json['data']);
  }).catch((error) => {
    console.log('There has been a problem with your fetch operation: ' + error);
    callback(error, {});
  });
}

export default ApiClient;



