<template>
  <div class="modal is-active">
    <div class="modal-background"></div>
    <div class="modal-card is-narrow">
      <header class="modal-card-head">
        <p class="modal-card-title">Congratulations!</p>
        <button class="delete is-large" aria-label="close" v-on:click="close"></button>
      </header>
      <section class="modal-card-body">
        <div class="is-flex">
          <div>You scored</div>
          <cards-svg :text="score" width="120" height="120"></cards-svg>
          <div>points</div>
        </div>
      </section>
    </div>
  </div>
</template>

<script>
  import CardsSvg from "./svg/CardsSvg.vue";

  export default {
    name: 'FinishDialog',

    components: {
      'cards-svg': CardsSvg
    },

    computed: {
      score: function () {
        return this.$store.getters.score;
      }
    },

    methods: {
      close: function () {
        this.$store.dispatch('returnToCategory', {score: this.score});
      }
    }
  }

</script>

<style lang="sass" scoped>
  @import "../values.sass"
  .modal-card-head
    border-top-left-radius: 10px
    border-top-right-radius: 10px
    background-color: $main_color_light
    border: 0
    .modal-card-title
      @include large_bold_font
      text-align: center
      padding-left: 54px // 34px + 20px

  .modal-card-body
    border-bottom-left-radius: 10px
    border-bottom-right-radius: 10px
    text-align: center
    .is-flex
      display: inline-flex !important
      div
        @include medium_default_font
        display: inline-block
        margin: auto 0
      svg
        margin: 0 10px

  @media (max-width: $tablet - 1px)
    .modal-card-head
      padding: 20px 10px
      .modal-card-title
        padding-left: 34px // 24px + 10px
        @include small_bold_font
      .is-large
        height: 24px
        max-height: 24px
        max-width: 24px
        min-height: 24px
        min-width: 24px
        width: 24px
    .modal-card-body
      .is-flex
        div
          @include small_default_font
        svg
          width: 60px
          height: 60px
</style>



