import Vue from 'vue'
import Router from 'vue-router'
import Categories from '../components/Categories.vue'
import Category from '../components/Category.vue'
import PageNotFound from '../components/PageNotFound.vue'
import Login from '../components/Login.vue'
import Registration from '../components/Registration.vue'

Vue.use(Router)

 const router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Categories,
      meta: {requiresAuth: true, title: 'Home'}
    },
    {
      path: '/login',
      name: 'Login',
      component: Login,
      meta: {redirectToHome: true, title: 'Login'}
    },
    // {
    //   path: '/registration',
    //   name: 'Registration',
    //   component: Registration,
    //   meta: {redirectToHome: true, title: 'Registration'}
    // },
    {
      path: '/categories',
      name: 'Categories',
      component: Categories,
      meta: {requiresAuth: true, title: 'Categories'}
    },
    {
      path: '/categories/:id',
      name: 'Category',
      component: Category,
      props: true,
      meta: {requiresAuth: true, title: 'Category'}
    },
    { path: "*", component: PageNotFound }
  ]
});

router.beforeEach((to, from, next) => {
  var obj = JSON.parse(localStorage.getItem('store')) || {auth: {isLoggedIn: false}};

  if (to.matched.some(record => record.meta.requiresAuth) && !obj.auth.isLoggedIn) {
    next({path: '/login', query: {redirect: to.fullPath}});
  } else {
    next();
  }

  if (to.matched.some(record => record.meta.redirectToHome)) {
    if (obj.auth.isLoggedIn) {
      next({name: 'Home'});
    } else {
      next();
    }
  }

  document.title = to.meta.title
});

export default router



