<template>
  <div class="columns is-mobile">
    <div class="column is-narrow stars-holder">
      <div v-for="(star, index) in stars">
        <svg class="star-svg" xmlns="http://www.w3.org/2000/svg" id="svg8" version="1.1"
             viewBox="-0.5 -0.5 7.614583 8.1437502"
             v-bind:style="initialRotate(index)">
          <g
            style="fill:none;stroke:#000000;stroke-opacity:1;opacity:1"
            id="layer1"
            transform="translate(0,-289.85623)">
            <path v-bind:class="star"
                  id="path825"
                  style="opacity:1;
                   fill-opacity:1;
                   fill-rule:nonzero;
                   stroke-width:0.4;
                   stroke-miterlimit:4;
                   stroke-dasharray:none;
                   stroke-opacity:1"
                  d="M 6.6145834,295.61091 4.2984093,295.44887 2.570732,297.00001 2.0091086,294.74713 1.7525766e-7,293.58334 1.969072,292.35302 l 0.48598,-2.2704 1.7785766,1.49251 2.3094606,-0.2394 -0.869851,2.15273 z"/>
          </g>
        </svg>
      </div>
    </div>
  </div>
</template>

<script>
  import CardsSvg from "./svg/CardsSvg.vue";

  export default {
    name: 'Stars',

    computed: {
      stars: function () {
        return this.$store.getters.stars;
      }
    },
    methods: {
      initialRotate: function (index) {
        return `transform: rotate(${index * 20}deg)`
      }
    }
  }

</script>

<style lang="sass" scoped>
  @import "../values.sass"
  .stars-holder
    margin: auto
    height: 40px
    padding: 0
    display: flex
    justify-content: space-around
    width: 80%
    .star-svg
      margin: auto
      width: 30px
      transition: all .7s ease-in-out

      &:hover
        transform: rotate(360deg) !important

      path
        stroke: $gray

        &.correct
          fill: $yellow

        &.incorrect
          fill: $red_dark

        &.none
          fill: lighten($gray, 50%)
</style>



