<template>
  <section class="section login">
    <div class="container">
      <div class="columns">
        <div class="column is-half is-offset-one-quarter">
          <div class="card">
            <div class="card-content">
              <h1 class="title">
                Welcome back!
              </h1>

              <form action="">
                <div class="field">
                  <label class="label">Email</label>
                  <div class="control has-icons-left">
                    <input v-bind:class="{'input': true, 'is-danger': errors.email}"
                           v-model="login.email"
                           type="email"
                           placeholder="example@mail.com"
                           v-on:keyup="cleanErrorKey('email')"
                    >
                    <span class="icon is-small is-left">
                      <i class="fa fa-envelope"></i>
                    </span>
                  </div>
                  <div class="help is-danger" v-if="errors.email">{{this.errors.email[0]}}</div>
                </div>

                <div class="field">
                  <label class="label">Password</label>
                  <div class="control">
                    <input v-bind:class="{'input': true, 'is-danger': errors.encrypted_password}"
                           v-model="login.password"
                           type="password"
                           placeholder="password"
                           v-on:keyup="cleanErrorKey('encrypted_password')"
                    >
                  </div>
                  <div class="help is-danger" v-if="errors.encrypted_password">{{this.errors.encrypted_password[0]}}
                  </div>
                </div>

                <div class="field is-grouped is-grouped-centered">
                  <p class="control">
                    <button type="button" v-on:click.prevent="signin"
                            class="button is-primary">
                      Sign In
                    </button>
                  </p>
                </div>

              </form>

            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>

  export default {
    name: 'Login',

    data: function () {
      return {
        login: {
          email: "",
          password: ""
        }
      }
    },

    methods: {
      signin: function () {
        this.$store.dispatch('signin', {body: this.login, router: this.$router});
      },

      cleanErrorKey: function (key) {
        if (this.errors[key]) {
          this.$store.dispatch("cleanErrorKey", {key: key})
        }
      }
    },

    computed: {
      errors: function () {
        return this.$store.state.errors
      }
    }
  }
</script>

<style lang="sass" scoped>
  @import "../values.sass"

  .title
    @include medium_bold_font

  +mobile
    .login
      padding-top: 1.5em
</style>



